.label{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
    font-size: 20px;
    padding: 10px 0;
    text-indent: 10px;
    position: relative;
}
.inputContainer{
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-align: center;
        align-items: center;
}
.input{
    position: relative;
    font-size: 18px;
    border: 2px solid var(--main-blue);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 5px;
    padding: 5px;
    width: 90%;
    margin: 5px 0;
    -webkit-transition: background-color 1s, color 1s;
    -o-transition: background-color 1s, color 1s;
    transition: background-color 1s, color 1s
}
.input:focus{
    background-color: var(--main-blue);
    color: white;
    -webkit-transition: background-color 1s, color 1s;
    -o-transition: background-color 1s, color 1s;
    transition: background-color 1s, color 1s
}
.submit{
    border: none;
    cursor: pointer;
    width: 25%;
    height: 40px;
    border-radius: 5px;
    background-color: var(--header-left);
    font-size: 20px;
    -ms-flex-item-align: center;
        align-self: center;
    -webkit-transition: color 1s;
    -o-transition: color 1s;
    transition: color 1s
}
.submit:hover{
    color: white;
    -webkit-transition: color 1s;
    -o-transition: color 1s;
    transition: color 1s
}
em {
    font-size: 14px;
    color: var(--main-blue);
}


